.select_wrapp {
  input[type="checkbox"],
  input[type="radio"] {
    display: inline;
    + label {
      display: inline;
      margin-left: 10px;
      &:before {
        display: none;
      }
    }
  }
}
//====================placeholder
::-webkit-input-placeholder {
  opacity: 1;
  color: @fontColor;
}

:-moz-placeholder {
  opacity: 1;
  color: @fontColor;
}

::-moz-placeholder {
  opacity: 1;
  color: @fontColor;
}

:-ms-input-placeholder {
  opacity: 1;
  color: @fontColor;
}

.placeholder {
  opacity: 1;
  color: @fontColor;
}

:focus::-webkit-input-placeholder {
  opacity: 0
}

:focus:-moz-placeholder {
  opacity: 0
}

:focus::-moz-placeholder {
  opacity: 0
}

:focus:-ms-input-placeholder {
  opacity: 0
}

:focus.placeholder {
  opacity: 0
}

//====================error inputs
input.si-error::-webkit-input-placeholder, textarea.si-error::-webkit-input-placeholder {
  color: @errorColor;
}

input.si-error::-moz-placeholder, textarea.si-error::-moz-placeholder {
  color: @errorColor;
}

input.si-error::-ms-input-placeholder, textarea.si-error::-ms-input-placeholder {
  color: @errorColor;
}

input.si-error.placeholder, textarea.si-error.placeholder {
  color: @errorColor;
}

input[type=text] {

  &.si-error {
    color: @errorColor !important;
    border-color: @errorColor !important;
  }
}

//====================label
label {
  cursor: pointer !important;
}

//====================text inputs
input[type=text] {
  -webkit-appearance: none;
  font: 18px/24px "DIN Condensed";
  display: block;
  width: 100%;
  height: 62px;
  color: @fontColor !important;
  background: #fff;
  border: 0;
  border-bottom: 2px solid @fontColor;
  margin-bottom: 18px;
  padding: 0 24px;
}

input[type=email],
input[type=tel] {
  @extend input[type=text];
}

//====================textarea
textarea {
  @extend input[type=text];
  resize: none !important;
  height: 202px;
  padding: 14px 24px;
}

//==================== select
.jq-selectbox-wrapper {
  display: block !important;
  width: 100% !important;
  position: relative;
  z-index: 30;

  
}
.main-schedule-filter .jq-selectbox.jqselect{
  display: inline-block !important;
  vertical-align: middle;
  width: 340px !important;
}
.jq-selectbox {
  display: block !important;
  width: 100% !important;

  li {
    .general-transition;
    color: @fontColor !important;

    &:hover,
    &:active,
    &.selected {
      background-color: #f3f3f3;
    }
  }
}

.jq-selectbox__select {
  display: block;
  width: 100%;
  font: 18px/62px 'DIN Condensed';
  color: @fontColor;
  height: 62px;
  padding: 0 72px 0 24px;
  border: 0 !important;
  border-bottom: 2px solid @fontColor !important;
  border-radius: 0;
  background: #fff !important;
  box-shadow: none !important;
  text-shadow: none !important;

  .main-schedule-filter & {
    font: 18px/42px 'DIN Condensed';
    height: 48px;
    padding: 0 52px 0 16px;
    border: 3px solid @accentColor !important;
  }
}

.jq-selectbox__select-text {
  height: 62px;

  .main-schedule-filter & {
    height: 42px;
  }
}

.jq-selectbox__trigger {
  .general-transition;
  width: 62px;
  background: #fff;
  border: 0 !important;
  border-bottom: 2px solid @fontColor !important;

  .main-schedule-filter & {
    .abs-center;
    left: auto;
    right: 3px;
    width: 42px;
    height: 42px;
    border-bottom: 0 !important;
  }

  &:hover,
  &:active,
  .jq-selectbox:hover &,
  .jq-selectbox:active &,
  .opened & {
    background: #f3f3f3;
  }
}

.jq-selectbox__trigger-arrow {
  .abs-center;
  border-top: 9px solid @fontColor;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  opacity: 1 !important;
  filter: alpha(opacity=100) !important;
}

.jq-selectbox__dropdown {
  color: @fontColor;
  font: 18px/24px 'DIN Condensed';
  top: 62px !important;
  width: 100%;
  background: #fff !important;
  border: 1px solid #f3f3f3 !important;
  border-radius: 0;
  z-index: 100;

  .main-schedule-filter & {
    top: 48px !important;
  }
}