//==================== GLOBAL HEADER
.header {
  z-index: 50;
  padding-top: 26px;
  margin-bottom: 92px;

  //==================== HEADER__CONTACTS
  &__contacts {
    .contacts__hours {
      .d_font_xs;
      list-style-type: none;
    }
    a {
      .b_font_m;
      margin-bottom: 6px;
    }
  }

  //==================== HEADER__LOGO
  &__logo {
    .b_font_m;
    text-transform: uppercase;

    &:hover .header__logo_top {
      color: @accentColor;
    }

    &:active .header__logo_top {
      color: @accentColor;
      .no-transition;
      top: 3px;
    }

    &:hover .header__logo_bottom ,
    &:active .header__logo_bottom {
      color: @fontColor !important;
      border-color: @fontColor !important;
      background: @accentColor !important;

      &::before {
        border-color: transparent transparent transparent @fontColor;
      }
      &::after {
        border-color: transparent transparent transparent @accentColor;
      }
    }

    //==================== HEADER__LOGO_TOP
    &_top {
      .general-transition;
      background: @fontColor;
      color: #fff;
      border: 2px solid @fontColor;
      margin-bottom: 18px;

      &:before {
        .general-transition;
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        left: -2px;
        bottom: -11px;
        border-width: 9px 9px 0 0;
        border-color: @fontColor transparent transparent transparent;
      }
    }

    //==================== HEADER__LOGO_BOTTOM
    &_bottom {
      color: @fontColor;
      border: 2px solid @fontColor;
      .general-transition;

      &:before,
      &:after {
        .general-transition;
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
      }

      &:after {
        top: -6px;
        left: 0;
        border-color: transparent transparent transparent #fff;
        border-width: 6px 0 0 6px;
        z-index: 2;
      }

      &:before {
        top: -11px;
        left: -2px;
        border-color: transparent transparent transparent @fontColor;
        border-width: 9px 0 0 9px;
        z-index: 1;
      }
    }

    &_top,
    &_bottom {
      .gutter(@indentXs);
      display: inline-block;
      vertical-align: top;
      line-height: 28px;
      position: relative;
      height: 28px;
    }
  }

  //==================== HEADER__NAV
  &__nav {
    &_top {
      .b_font_s;
      li {
        position: relative;
        margin-right: 5px;
        .icon {
          font-size: 1.8em;
          margin-right: 5px;
          vertical-align: -10px;
        }
      }
    }
    &_bottom {
      .b_font_m;
      text-transform: uppercase;
      margin-top: 25px;
      .rowMargin(-@indentMd);
      .icon {
        font-size: 1.2em;
        vertical-align: middle;
      }
      li {
        margin-left: 5px;
        margin-right: 5px;
      }
    }
  }
}