@font-face {
  .font-face('DIN Condensed', ~'../fonts/DINCondensed-Regular', normal, normal);
}
@font-face {
  .font-face('DIN Condensed C', ~'../fonts/DINCondensedC', normal, normal);
}
// xs 16px
// s 18px
// m 24px
// l 30px
// H1 48px
// H1 60px
// Xl 150px
.b_font_xs { .baseFont(14px, normal, 1, none) }
.b_font_s { .baseFont(16px, normal, 1, none) }
.b_font_m { .baseFont(18px, normal, 1, none) }
.b_font_l { .baseFont(24px, normal, 1.5, none) }
.b_font_xl { .baseFont(30px, normal, 1, none) }
.b_font_h4 { .baseFont(18px, normal, 1.3, uppercase) }
.b_font_h3 { .baseFont(30px, normal, 1.1, uppercase) }
.b_font_h2 { .baseFont(48px, normal, 1, uppercase) }
.b_font_h1 { .baseFont(60px, normal, 1, uppercase) }
.b_font_xxl { .baseFont(150px, normal, 1, uppercase) }

.d_font_xs { .defaultFont(14px, normal, 1, none) }
.d_font_xs_i { .defaultFont(14px, italic, 1, none) }
._bold { font-weight: bold !important;}
._up {text-transform: uppercase;}
.d_font_s { .defaultFont(16px, normal, 1, none) }
.d_font_m { .defaultFont(18px, normal, 1.5, none) }
.d_font_l { .defaultFont(20px, normal, 1.2, none) }
.d_font_xl { .defaultFont(30px, normal, 1.5, none) }
.d_font_h2 { .defaultFont(48px, normal, 1, uppercase) }
.d_font_h1 { .defaultFont(60px, normal, 1, uppercase) }
.d_font_xxl { .defaultFont(150px, normal, 1, uppercase) }




//==================== TEXT ON LIGHT BG
.light-text {
  display: inline-block;
  color: @fontColor;
  background: #fff;
  padding: 0 5px;
  position: relative;
  left: -5px;
}

//==================== TEXT ON DARK BG
.dark-text {
  .light-text;
  color: #fff;
  background: @fontColor;
}

//==================== TEXT ON PRIMARY BG
.primary-text {
  .light-text;
  background: @primaryColor;
}

//==================== TEXT ON ACCENT BG
.accent-margin {
  margin-bottom: 5px;
}
.accent-text {
  .light-text;
  background: @accentColor;
  &_i {
    .d_font_xs_i;
    line-height: 1.5;
  }
  &_l {
    .b_font_h2;
    text-transform: uppercase;
    .accent-text {
      padding-left: 2px;
    }
  }
  &_m {
    .b_font_l;
    line-height: 1;
    text-transform: uppercase;
  }
  &_s {
    .b_font_m;
  }
  &_xs {
    .d_font_xs;
  }
  &_line {
    position: relative;
    &:before {
      content: "";
      position: absolute;
      height: 4px;
      width: 28px;
      background: @accentColor;
      right: 100%;
      margin-right: 6px;
      top: 50%;
      margin-top: -2px;
    }
  }
}

//==================== LINK
a {
  .general-transition;
  color: @linkColor;
  cursor: pointer !important;
  display: inline-block;
  line-height: 1.2;
  text-decoration: none;
  &.dark {
    border-color: @fontColor;
    color: @fontColor;
  }

  &.light {
    border-color: #fff;
    color: #fff;
  }

  &.primary {
    border-color: @primaryColor;
    color: @primaryColor;
  }

  &.bold {
    .b_font_m;
  }

  &.offtop {
    border-color: @offtopColor;
    color: @offtopColor;
  }

  &:hover,
  &:active {
    border-color: @accentColor;
    color: @accentColor;
  }

  &.accent {
    border-color: @accentColor;
    color: @accentColor;

    &:hover,
    &:active {
      border-color: @linkColor;
      color: @linkColor;
    }
  }
}


//==================== BLOCKQUOTE
blockquote {
  display: inline-block;
  // font: 24px/30px "DIN Condensed";
  .b_font_l;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  position: relative;

  p {
    margin-bottom: 18px;
  }

  &::before {
    content: "/";
    // font: 48px/1 "DIN Condensed";
    .b_font_h2;
    position: absolute;
    top: -13px;
    left: -30px;
  }

  &::after {
    content: "/";
    // font: 48px/1 "DIN Condensed";
    .b_font_h2;
    position: absolute;
    bottom: -13px;
    right: -20px;
  }
}
