@import "_base";
.u_alignCenter {
  text-align: center;
}
.u_alignRight {
  text-align: right;
}
.u_alignLeft {
  text-align: left;
}
.u_floatLeft {
  float: left;
}
.u_floatRight {
  float: right;
}
.u_block {
  display: block;
}
.u_prl {
  position: relative;
}
.col-position {
  flex-direction: column;
  display: flex;
}
.bottom {
  margin-top: auto;
}
.fx_x {
  .m_posX(relative);
}
.fx_xy {
  .m_posXY(relative);
}
.fx_y {
  .m_posY(relative);
}
.general-transition {
  will-change: transform;
  .translate3d(0, 0, 0);
  .transition(all .3s ease-in-out);
}

.no-transition {
  will-change: none;
  transform: none;
  transition: none;
}

.no-transform {
  will-change: none;
  transform: none;
}
.abs-center {
  position: absolute !important;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}