.add-slider {
  height: 221px;
  display: none;
  padding: @indentXs;
  position: relative;
  border: 3px solid @fontColor;
  background: #fff;
  padding: 24px 24px 18px 24px;
  line-height: 1.1;
  &__buttons {
    position: absolute;
    top: -75px;
    right: @indentXs;
    margin: 0 -8px;
    .btn {
      margin: 0 8px;
    }
  }
  .icon {
    color: @accentColor;
    font-size: 5em;
    position: absolute;
    top: 12px;
    right: 12px;
  }
  &:before,
  &:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
  }
  &:before {
    border-color: transparent transparent transparent @fontColor;
    border-width: 51px 0 0 51px;
    left: -3px;
    top: -51px;
  }
  &:after {
    top: -43px;
    left: 0;
    border-width: 43px 0 0 43px;
    border-color: transparent transparent transparent #fff;
    z-index: 10;
  }
  &__description {
    list-style-type: none;
    li {
      .d_font_l;
    }
  }
}