.slide {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  &__title {
    margin-bottom: 4px;
  }
  &__link {
    display: block;
    padding-bottom: 48px;
    height: 100%;
    width: 100%;
    position: relative;
    border-bottom: 0;
    &:hover {
      color: #231f20;
      .text-line {
        background: #ffea5f;
        -webkit-transition: none;
        -moz-transition: none;
        transition: none;
      }
    }
  }
  &__inner {
    .fx_y();
    padding-right: @indentMd;
    width: 360px;
  }
}
.slider {
  position: relative;
  margin-bottom: 35px;
  line-height: 1.1;
  &_skew {
    .fotorama__wrap {
      &:after {
        content: '';
        position: absolute;
        height: 48px;
        bottom: 12px;
        left: 24px;
        right: -100%;
        background: #fff;
        .skewX(-45deg);
        display: block;
      }
    }
  }
  //============= FOTORAMA CUSTOM STYLE
  .fotorama {
    &__nav-wrap {
      position: relative;
      z-index: 2;
      margin-top: -12px;
    }
    &__nav {
      text-align: left;
      padding-left: 48px;
      &--dots .fotorama__nav__frame {
        width: 24px;
        height: 24px;
        line-height: 24px;
        text-align: center;
      }
      &__frame.fotorama__active .fotorama__dot {
        background-color: @fontColor;
        border: none;
        width: 12px;
        height: 12px;
      }
    }

    &__dot {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      border: 2px solid @fontColor;
      background: #fff;
      vertical-align: middle;
      display: inline-block;
      top: auto;
      left: auto;
      .general-transition;
      &:hover {
        background-color: @accentColor;
      }
    }
    &__arr,
    &__fullscreen-icon,
    &__video-close,
    &__video-play {
      background: url(../img/fotorama.png) no-repeat
    }
  }
}

.add-slider-wrap {
  height: 345px;
  .slide {
    display: none;
    // float: right;
    // width: 100%;
  }
}