// MARGINS
@indentXl: 45px;
@indentXs: 30px;
@indentMd: 15px;
@indentSm: 5px;
// GLOBAL WIDTH
@desktopXs: 1200px;
// COLOR
@fontColor: #000;
@primaryColor: #000;
@accentColor: #facf00;
@offtopColor: #bbb;
@linkColor: #00a1dd;
@errorColor: #cb0707;

.white {
  color: #fff;
}
//color in primary
.primary {
  color: @primaryColor;
}

//color in complementary
.accent {
  color: @accentColor;
}

//color in offtop
.offtop {
  color: @offtopColor;
}
.border-accent {
  border: solid 5px @accentColor;
  padding-top: @indentMd;
  padding-bottom: @indentMd;
}


