.baseFont(@fSize, @fStyle, @fLine, @transform) {
  font-family: 'DIN Condensed', sans-serif;
  font-size: @fSize;
  font-style: @fStyle;
  line-height: @fLine;
  text-transform: @transform;
  font-weight: normal;
}
.defaultFont(@fSize, @fStyle, @fLine, @transform) {
  font-family: 'Times New Roman', serif;
  font-size: @fSize;
  font-style: @fStyle;
  line-height: @fLine;
  text-transform: @transform;
  font-weight: normal;
}

.desktop-xs(@width) {
  width: @width;
}
.rowMargin(@margin) {
  margin-left: @margin;
  margin-right: @margin;
}
.gutter(@padding) {
  padding-left: @padding;
  padding-right: @padding;
}
.paddingXY(@paddindXY) {
  padding-left: @paddindXY;
  padding-right: @paddindXY;
  padding-top: @paddindXY;
  padding-bottom: @paddindXY;
}


.m_posY(@position) {
  position: @position;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-font-smoothing: antialiased;
}
.m_posX(@position) {
  position: @position;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  -webkit-font-smoothing: antialiased;
}
.m_posXY(@position) {
  position: @position;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-font-smoothing: antialiased;
}
.row_y {
  align-items: center;
}
// .anim(@willChange, @animation, @transform) {
//   will-change: @willChange;
//   transform: @transform;
//   animation: @animation;
// }
.anim(@willChange, @animation, @transform) {
  will-change: @willChange;
  animation: @animation;
  transform: @transform;
}
.animation-duration(@duration) {
  -webkit-animation-duration: @duration;
  -o-animation-duration: @duration;
  -moz-animation-duration: @duration;
  animation-duration: @duration;
}

//==================== hover icon animation
.mixin() {
  @width:  100%;
  @height: 200px;
}

.caller {
  .mixin();
  width:  @width;
  height: @height;
}
.ani-hover() {

  &:hover {

    [class*="-image"] .icon,
    [class*="-image"] svg {
      .anim(transform, buzz-out 1 linear 1.0s, translate3d(0, 0, 0));
    }
  }
}

.li-counter-reset(@content1, @content2) {
  position: relative;

  &::before {
    .abs-center;
    bottom: auto;
    content: "@{content1}" counter(li) "@{content2}";
    counter-increment: li;
  }
}
.ol-counter-reset {
  counter-reset: li;
  padding: 0;
  margin: 0;
  li {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}