//====================buttons
.btn-holder {
  position: relative;
  z-index: 1;
}

.btn {
  .general-transition;
  display: inline-block;
  cursor: pointer;
  border: 0;
  text-decoration: none !important;
  text-align: center;
  white-space: nowrap;
  position: relative;
  z-index: 2;
  color: #fff !important;
  .b_font_l;
  line-height: 80px;
  background: @primaryColor;
  height: 80px;
  padding: 0 20px;

  &:hover,
  &:active {
    color: @fontColor !important;
    background: @accentColor;
  }

  &:active {
    .no-transition;
    top: 3px;
  }
}

.btn-nav {
  height: 27px;
  color: @fontColor;
  position: relative;
  padding: 6px 10px 2px 10px;
  .general-transition;
  &:after {
    content: "";
    position: absolute;
    top: 100%;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent transparent transparent transparent;
    left: 0;
    border-width: 9px 9px 0 0;
    .general-transition;
  }
  &:active {
    .no-transition;
    top: 3px;
  }
  &:hover,
  &:active {
    color: @fontColor !important;
    border-color: @fontColor !important;
    background: @accentColor !important;
    span {
      border-bottom: none;
    }
    &:after {
      border-style: solid;
      border-color: @accentColor transparent transparent transparent;
    }
  }
}

.btn-small {
  .b_font_l;
  line-height: 62px;
  height: 62px;
}

.btn-accent {
  color: @fontColor !important;
  background: @accentColor;

  &:hover,
  &:active {
    color: #fff !important;
    background: @primaryColor;
  }
}

.btn-bordered {
  color: @fontColor !important;
  .b_font_xs;
  line-height: 24px;
  text-transform: uppercase;
  background: #fff;
  border: 2px solid @accentColor;
  height: 26px;
  padding: 0 10px;

  &::before,
  &::after {
    .general-transition;
    content: "";
    position: absolute;
    bottom: 100%;
    width: 0;
    height: 0;
    border-style: solid;
  }

  &::before {
    left: -2px;
    border-width: 12px 0 0 12px;
    border-color: transparent transparent transparent @accentColor;
    z-index: 1;
  }

  &::after {
    left: 0;
    border-width: 8px 0 0 8px;
    border-color: transparent transparent transparent #fff;
    z-index: 2;
  }

  &:hover,
  &:active {
    color: @fontColor !important;
    border-color: @fontColor !important;
    background: @accentColor !important;

    &::before {
      border-color: transparent transparent transparent @fontColor;
    }

    &::after {
      border-color: transparent transparent transparent @accentColor;
    }
  }

  &.btn-medium {
    font: 18px/30px "DIN Condensed";
    border: 3px solid @accentColor;
    height: 35px;
    padding: 0 15px;

    &::before,
    &::after {
      top: 100%;
    }

    &::before {
      left: -3px;
      border-width: 17px 17px 0 0;
      border-color: @accentColor transparent transparent transparent;
    }

    &::after {
      border-width: 9px 9px 0 0;
      border-color: #fff transparent transparent transparent;
    }

    &:hover,
    &:active {

      &::before {
        border-color: @fontColor transparent transparent transparent;
      }

      &::after {
        border-color: @accentColor transparent transparent transparent;
      }
    }
  }

  &.btn-dark {
    border-color: @fontColor;

    &::before {
      border-color: transparent transparent transparent @fontColor;
    }

    &:hover,
    &:active {

      &::before {
        border-color: transparent transparent transparent @fontColor;
      }

      &::after {
        border-color: transparent transparent transparent @accentColor;
      }
    }
    &_medium {
      .b_font_m;
      line-height: 26px;
      text-transform: uppercase;
      height: 26px;
    }
  }
  
  &.btn-accent {
    color: @fontColor !important;
    background: @accentColor;

    &:after {
      border-color: transparent transparent transparent @accentColor;
    }

    &:hover,
    &:active {
      color: #fff !important;
      background: @primaryColor;
    }
  }

  &.btn-big {
    font: 25px/40px "DIN Condensed";
    border: 4px solid @accentColor;
    height: 46px;
    padding: 0 15px;

    &::before {
      left: -4px;
      border-width: 22px 0 0 22px;
      border-color: transparent transparent transparent @accentColor;
    }

    &::after {
      border-width: 12px 0 0 12px;
      border-color: transparent transparent transparent #fff;
    }

    &:hover,
    &:active {

      &::before {
        border-color: transparent transparent transparent @fontColor;
      }

      &::after {
        border-color: transparent transparent transparent @accentColor;
      }
    }
  }

  &.btn-huge {
    font: 42px/72px "DIN Condensed";
    background: @accentColor;
    border: 3px solid @fontColor;
    height: 78px;
    padding: 0 33px;

    &::before {
      left: -3px;
      border-width: 33px 0 0 33px;
      border-color: transparent transparent transparent @fontColor;
    }

    &::after {
      border-width: 26px 0 0 26px;
      border-color: transparent transparent transparent @accentColor;
    }

    &:hover,
    &:active {
      color: #fff !important;
      background: @fontColor !important;

      &::before {
        border-color: transparent transparent transparent @fontColor;
      }

      &::after {
        border-color: transparent transparent transparent @fontColor;
      }
    }
  }

  &.btn-with-icon {
    font: 25px/66px "DIN Condensed";
    border: 3px solid @accentColor;
    height: 66px;
    padding: 0 12px;

    .icon {
      margin-right: 4px;
      position: relative;
      top: -5px;
    }

    &::before {
      left: -3px;
      border-width: 21px 0 0 21px;
      border-color: transparent transparent transparent @accentColor;
    }

    &::after {
      border-width: 13px 0 0 13px;
      border-color: transparent transparent transparent #fff;
    }

    &:hover,
    &:active {

      &::before {
        border-color: transparent transparent transparent @fontColor;
      }

      &::after {
        border-color: transparent transparent transparent @accentColor;
      }
    }
  }
}

//====================button effects
.btn-ghost {
  .anim(transform, si-button-pulse 0.8s 0s ease-out 1, translate3d(0, 0, 0));
  box-shadow: none;
}

.btn-curtain {

  &::after {
    // .anim(transform, si-button-pulse 0.8s 0s ease-out 1, translate3d(0, 0, 0));
    // @extend .btn-lift;
    .anim(transform, btn-lift 0.3s ease forwards, translate3d(0, 0, 0));
    background: @accentColor;
    top: auto;
    bottom: auto;
  }

  &:hover,
  &:active {
    color: #fff !important;
    border-color: @accentColor;

    &::after {
      .anim(transform, btn-fall 0.3s ease forwards, translate3d(0, 0, 0));
      // @extend .btn-fall;
    }
  }
}

.btn-border-expand {
  position: relative;

  &::after {
    .abs-center;
    .anim(transform, border-shrink .3s ease-out both, translate3d(0, 0, 0));
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
  }

  &:hover,
  &:active {

    &::after {
      .anim(transform, border-expand .3s ease-out both .15s, translate3d(0, 0, 0));
      width: calc(100% + 12px) !important;
      height: calc(100% + 12px) !important;
      left: -6px;
    }
  }
}