.tiles {
  a {
    margin-top: @indentSm;
  }
  .accent-text_i {
    margin-bottom: 0;
  }
  .icon {
    font-size: 1.5em;
    // .m_posY(absolute);
    position: absolute;
    top: -5px;
    right: -@indentMd;
    left: auto;
  }
  .accent-text_xs {
    margin: 10px 0;
  }
  &_row {
    margin: @indentXl 0;
  }
  &_pad {
    padding-top: 32px;
  }
  &_h4 {
    margin-top: 0;
    height: 40px;
  }
  &_figure {
    height: 120px;
    width: 131px;
  }
  &_sm {
    .bg_circle,
    .bg_square {
      margin-top: 0;
      top: -27px;
    }
    .accent-text_i {
      margin-top: 10px;
    }
    h4 {
      height: 46px;
    }
    .bg_triangle {
      margin-top: 0;
      top: -70px;
    }
    .tiles_row {
      margin: 60px 0;
    }
    .bg_romb {
      margin-top: 0;
      width: 95px;
      height: 95px;
      top: -47px;
    }
  }
}
.head_romb {
  margin-top: -10px;
}