//==================== section schedule-inner
.section-schedule-inner {
  padding: 50px 0 0;
  position: relative;
  z-index: 5;

  h2 {
    margin-bottom: 116px;

    .icon {
      top: -42px;
      left: 796px;
    }

    .arrow-block {
      width: 221px;
      top: -28px;
      left: 15px;
    }
  }
}

