.main-schedule-info {
  font: 24px/36px "DIN Condensed";
  text-transform: uppercase;
  // padding-left: 140px;
  margin-bottom: 69px;
}

.main-schedule-filter {
  // padding-left: 140px;
  margin-bottom: 59px;
}

.main-schedule-filter-text {
  display: inline-block;
  vertical-align: middle;
  font: 18px/1 "DIN Condensed";
  text-transform: uppercase;
  margin-right: 90px;
}

.schedule-table {
  width: 100%;
  th {
    font: 18px/1 "DIN Condensed";
    text-transform: uppercase;
    text-align: left;
    padding: 0 22px 18px;
  }
  td {
    padding: 15px 22px;
  }
  tr {
    &:nth-child(even) td {
      background: #f5f5f5;
    }
  }
}

.schedule-table-date {
  font: 18px/1 "Times New Roman";
  width: 250px;

  .date-item {
    color: #575757;

    &.active {
      color: @fontColor;
      font-weight: bold;
    }

    &:not(:first-child) {
      margin-top: 12px;
    }
  }
}

.schedule-table-info {

  a {
    font: 24px/30px "Times New Roman";
    margin-bottom: 5px;
  }
}

.course-summary {
  color: #575757;
}

.schedule-table-link {
  width: 154px;
}
