.inner-results-item {
  padding: 20px 0 0 20px;
  position: relative;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border: 3px dashed #f3f3f3;
    width: 194px;
    height: 194px;
    z-index: -1;
  }
  .b_font_h4 {
    margin-bottom: 20px;
  }
}