
.doubt-block {
  .ol-counter-reset;
  position: relative;
  z-index: 5;
  li:not(.row) {
    .li-counter-reset('.0', '');
    margin-bottom: 167px;

    &::before {
      font: 36px/1 "DIN Condensed";
      top: auto;
      bottom: -99px;
      left: -20px;
    }

    // &.i-1 {
    //   left: -20px;
    // }

    // &.i-2 {
    //   margin-top: -205px;
    //   left: -10px;
    // }

    // &.i-3 {
    //   left: -10px;
    // }
  }
}

.doubt-item {
  padding: 60px 199px 69px 40px;
  position: relative;
  z-index: 1;
}

.doubt-border-block {
  .abs-center;
  right: auto;
  width: 320px;
  border: 8px solid #f3f3f3;
  position: relative;
  z-index: -1;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 100%;
    width: 0;
    height: 0;
    border-style: solid;
  }

  &::before {
    left: -8px;
    border-width: 48px 48px 0 0;
    border-color: #f3f3f3 transparent transparent transparent;
    z-index: 1;
  }

  &::after {
    left: 0;
    border-width: 30px 30px 0 0;
    border-color: #fff transparent transparent transparent;
    z-index: 2;
  }
}

.doubt-title {
  font: 24px/25px "DIN Condensed";
  text-transform: uppercase;
  letter-spacing: -.05pt;
  margin-bottom: 26px;

  .accent-text {
    margin-bottom: 5px;
  }
}