//====================navigation list - tabs
.nav.nav-tabs {
  width: 100%;
  padding-left: 80px;
  margin-bottom: 83px;
}

.tab-link {
  .general-transition;
  font: 18px/43px "DIN Condensed";
  text-transform: uppercase;
  height: 46px;
  border: 3px solid #ebebeb;
  cursor: pointer !important;
  position: relative;
  padding: 0 24px;

  &:not(:first-child) {
    margin-left: 16px;
  }

  &::before,
  &::after {
    .general-transition;
    content: "";
    position: absolute;
    top: 100%;
    width: 0;
    height: 0;
    border-style: solid;
  }

  &::before {
    left: -3px;
    border-width: 22px 22px 0 0;
    border-color: #ebebeb transparent transparent transparent;
    z-index: 1;
  }

  &::after {
    left: 0;
    border-width: 14px 14px 0 0;
    border-color: #fff transparent transparent transparent;
    z-index: 2;
  }

  &:hover,
  &:active,
  &.active {
    border-color: @accentColor;

    &::before {
      border-color: @accentColor transparent transparent transparent;
    }
  }
}

.tabs-content-container {
  overflow: hidden;
  margin: 0 -10px;
}

.tab-content {
  .general-transition;
  opacity: 0;
  height: 0;
  position: relative;
  z-index: 5;
  overflow: hidden;
  padding: 0 10px 10px;

  &.active {
    opacity: 1;
    height: auto;
    z-index: 10;
  }
}


.course-tutor {
  width: 780px;
  margin: 0 auto;
}