//====================swiper
.swiper-button-prev {
  .general-transition;
  .icon {
    font-size: 3em;
  }
  width: 12px;
  height: 54px;
  margin-top: 0;
  background: none !important;
  left: 0;
  background-position: 0 0;
  &:hover,
  &:active {
    .translateX(-5px);
    color: @accentColor;
  }
}

.swiper-button-next {
  .swiper-button-prev;
  left: auto;
  right: 0;

  &:hover,
  &:active {
    .translateX(5px);
    color: @accentColor;
  }
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 15px;

  .swiper-pagination-bullet {
    .general-transition;
    background: #7f7f7f;
    width: 14px;
    height: 14px;
    opacity: 1 !important;
    margin: 0 6px;

    &:hover,
    &:active,
    &.swiper-pagination-bullet-active {
      background: @accentColor;
    }
  }
}
