// #MEDIA
@highdensity: ~"only screen and (-webkit-min-device-pixel-ratio: 1.5)",
              ~"only screen and (min--moz-device-pixel-ratio: 1.5)",
              ~"only screen and (-o-min-device-pixel-ratio: 3/2)",
              ~"only screen and (min-device-pixel-ratio: 1.5)";
@mobile:      ~"only screen and (max-width: 529px)";
@tablet:      ~"only screen and (min-width: 530px) and (max-width: 949px)";
@desktop:     ~"only screen and (min-width: 950px) and (max-width: 1128px)";
@desktopXl:  ~"only screen and (min-width: 1129px)";

@media (max-width: 360px) {
  .container {
    width: 320px;
    padding-left: @indentMd;
    padding-right: @indentMd;
  }
  .header {
    margin-bottom: 25px;
  }
  .header__logo {
    display: block;
    text-align: center;
    margin-bottom: 15px;
  }
  .header__nav_top li {
    margin-right: 15px;
  .icon {
      margin-right: 10px;
    }
    a {
       font-size: 18px;
     }
  }
  .header__nav_bottom {
    font-size: 15px;
    margin-right: -9px;
    margin-left: -9px;
    li {
      margin-left: 0;
      margin-right: 0;
      a {
        padding: 0 9px;
      }
    }
    .icon {
       display: none;
     }
  }
  .header__contacts {
    margin-top: 15px;
    text-align: right;
  }
  .slide__inner {
    float: none;
    width: 100%;
    text-align: center;
    padding-left: 15px;
    .b_font_h1 {
      .accent-text_l {
        font-size: 35px;
      }
    }
    span {
      display: block;
      margin-bottom: 5px;
    }
  }
  .fotorama__nav-wrap {
    display: none;
  }
  .slider_skew .fotorama__wrap:after {
    bottom: 0;
  }
  .add-slider__buttons {
    top: -54px;
  }
  .add-slider {
    height: 350px;
    .icon-stamp-2 {
      display: none;
    }
  }
  h2 {
    font-size: 35px;
    &.heading {
      padding-top: 10px;
      padding-bottom: 45px;
      text-align: center;
    }
  }
  h2 .big-text {
    font-size: 90px;
  }
  .heading_square {
    height: 80px;
  }
  .heading_icon {
    display: none;
  }
  .heading_decor:before,
  .heading_decor:after {
    display: none;
  }
  .tiles {
    .icon {
      .rotate(90deg);
      right: 15px;
    }
    .col {
      margin: 15px 0;
    }
    .col:empty {
      display: none;
    }
    &_row {
      margin: 0;
    }
  }
  .form-1 .form-text-block ul.marked {
    margin-left: 0;
  }
  .form-1 .btn {
    width: 100%;
  }
  .effect-image {
    margin-bottom: 20px;
    width: 70px;
    height: 61px;
    float: left;
    margin-right: 10px;
  }
  .effect-title {
    font-size: 25px;
    padding-left: 80px;
  }
  .effect-block {
    br {
      display: none;
    }
  }
  .who-learn {
    // .fotorama__html {
    //   height: 170px;
    // }
    margin-bottom: 0;
    .slide_wrap {
      height: 170px;
    }
    .fotorama__wrap {
      height: 170px !important;
    }
  }
  .main-review-holder {
    .btn-holder {
      margin-top: 30px;
      margin-left: 30px;
    }
    blockquote {
      margin-left: 30px;
    }
  }
  .main-review-slider-holder {
    padding-left: 0;
    padding-right: 0;
    margin-top: 40px;
    .icon {
      fill: #fff;
    }
  }
  .main-review-block {
    padding-bottom: 0;
  }
  .swiper-pagination {
    display: none;
  }
  .doubt-item {
    padding: 15px;
  }
  .doubt-block li:not(.row)::before {
    display: none;
  }
  .doubt-block li:not(.row) {
    margin-bottom: 33px;
  }
  .teach_tabs {
    &.row {flex-direction: row; flex-wrap: wrap; margin: 0 -5px}
    .col {
      flex: 1;
      // margin: -5px;
      padding: 5px 0;
    }
    img {
      display: none;
    }
    h4.heading {
      text-align: center;
      .accent-text;
      width: 100%;
    }
  }
  .tiles_pad {
    margin-top: 15px;
    &_i {
      &.row {flex-direction: row; flex-wrap: wrap;}
      .col {
        flex: 0 0 50%;
      }
    }
  }
  .main-schedule {
    &-info {
      margin-bottom: 25px;
    }
    &-filter {
      margin-bottom: 25px;
      &-text {
        margin-bottom: 15px;
      }
      .jq-selectbox.jqselect {
        width: 275px !important;
      }
    }
  }
  .schedule-table {
    width: 290px;
    td {
      width: 100%;
      display: block;
    }
  }
  .section-contacts  {
    margin-bottom: 25px;
  }
  .contacts-address {
    padding-left: 0;
    margin-bottom: 25px;
  }
  .contacts-text-block {
    padding-left: 0;
  }
  .map-holder {
    margin-bottom: 25px;
  }
  .layout-footer {
    text-align: center;
    .copyright {
      margin-top: 15px;
    }
    .social {
      text-align: center;
      margin: 25px 0;
    }
    .si-phone {
     text-align: center;
    }
  }

  // COURSE
  .course-band__col_left {
    padding-left: 15px;
  }
  .course-band__col_right {
    padding-right: 15px;
  }
  .course-band {
    background-size: 320px auto;
    background: transparent;
    .section-indent;
    padding-top: 0;
    min-height: 825px;
    .more-form-content {
      margin-top: -30px;
    }
  }
  .course-band__col_right {
    margin-top: 15px;
    padding: 5px 15px;
    background-color: #000;
  }
  .course-band__price {
    margin-bottom: 5px;
  }
  .course-band__order {
    margin-top: 50px;
    width: 100%;
    margin-left: 0;
    .start-text {
      text-align: center;
    }
    .col {
      width: 100%;
    }
    .btn-huge {
      width: 100%;
      font-size: 31px;
      line-height: 31px;
      height: 65px;
      margin-bottom: 15px;
    }
  }
  .course-band__note {
    margin-top: 0;
    width: 100%;
    height: 250px;
    position: absolute;
    top: 128px;
    span {
      display: block;
    }
    h4 {
      font-size: 34px;
      .m_posXY(absolute);
      width: 250px;
      height: 250px;
      padding-top: 64px;
      margin: 0;
    }
    .bg_circle {
      .m_posXY(absolute);
      width: 250px;
      margin: 0;
      height: 250px;
    }
  }


  .more-form {
    &-content {
      background-color: #fff;
      z-index: 100;
      position: relative;
      .btn {
        margin-top: 25px;
        margin-bottom: 25px;
      }
    }
  }
  .course-advantage-item {
    font: 21px/30px "DIN Condensed";
    padding-left: 68px;
    padding-top: 0;
    margin-bottom: 28px;
    &:before {
      top: 15px;
    }
  }
  .section-course-advantages-inner {
    .arrow-block {
      top: 3px;
    }
    .heading {
      padding-top: 0;
      text-align: center;
      font-size: 21px;
      padding-bottom: 45px;
      &_decor {
        display: block;
      }
      &_square {
        top: -15px;
      }
      .b_font_h3 {
        font-size: 18px;
        margin-top: 20px;
        width: 80%;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
  .inner-results {
    .btn {
      margin-top: -45px;
      margin-bottom: 0;
      width: 100%;
    }
    &-block {
      margin-top: 0;
      &.row {
        display: block;
      }
    }
    &-item {
      // margin-bottom: 40px;
      height: 265px;
      &:before {
        min-height: 235px;
        width: 100%;
      }
    }
    .heading {
      font-size: 21px;
      .b_font_h3 {
        margin-top: 20px;
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        font-size: 18px;
      }
    }
    .heading_square {
      top: -15px;
    }
  }
  .section-course-reviews-inner {
    .btn {
      width: 100%;
      font-size: 18px;
      height: 40px;
    }
    .course-review-image {
      img {
        margin: 0 auto;
        display: block;
      }
    }
    .col {
      margin-bottom: 30px;
    }
    .heading {
      font-size: 26px;
      padding-bottom: 80px;
      &_decor {
        display: block;
      }
    }
  }
  .heading_square {
    height: 65px;
    width: 100%;
    left: 0;
  }
  .form-2 .form-input-block {
    width: 100%;

  }
  .form-video {
    height: auto;
    margin-top: -175px;
    padding-top: 259px;
    background-size: 1600px auto;
    h2 {
      font-size: 30px;
      padding-top: 0;
    }
  }
  .section-course-about-inner {
    .btn.show-video {
      width: 100%;
      display: block;
    }
    .col {
      display: block;
      width: 320px;
    }
    .tutor-image {
      margin-bottom: 20px;
      img {
        width: 290px;
      }
    }
    .heading {
      font-size: 30px;
      padding-bottom: 60px;
      &_decor {
        display: block;
      }
      &_square {
        top: -21px;
      }
    }
    .course-people-item {
      margin-bottom: 35px;
      br{
        display: none;
      }
    }
  }
  .course-summary-item {
    margin-bottom: 15px;
  }
  .course-summary-image {
    left: 0;
  }
  .course-lesson-item {
    margin-bottom: 25px;
    br {
      display: none;
    }
  }
  .course-tabs-block {
    .tutor-blockquote {
      width: 290px;
      display: block;
      &:before {
        top: -14px;
        left: -10px;
      }
      &:after {
        bottom: -18px;
        right: -5px;
      }
    }
    .nav-tabs {
      padding-left: 0;
      width: 100%;
      display: block;
      margin-bottom: 40px;
      .tab-link {
        width: 100%;
        display: block;
        margin-bottom: 25px;
        margin-left: 0;
      }
    }
  }
  .section-course-another-inner {
    .heading {
      font-size: 21px;
      &_square {
        top: -15px;
      }
    }
    .col {
      margin-bottom: 35px;
    }
  }
}