//====================icons
.iconPng {
  display: inline-block;
  overflow: hidden;
  text-indent: -9999px;
  background: url('http://hellobrand.ru/25hour/images/sprite.png') no-repeat transparent;
  position: relative;
  vertical-align: middle;
}

.will-change {
  background: none;

  &::before,
  &::after {
    @include general-transition;
    @include abs-center;
    content: "";
    overflow: hidden;
    background: url('http://hellobrand.ru/25hour/images/sprite.png') no-repeat transparent;
  }

  &::before {
    opacity: 1;
  }

  &::after {
    opacity: 0;
  }

  &:hover,
  &:active,
  &.active {

    &::before {
      opacity: 0;
    }

    &::after {
      opacity: 1;
    }
  }
}

.close-popup {
  @extend .will-change;
  width: 16px;
  height: 16px;

  &::before {
    background-position: -123px -601px !important;
  }

  &::after {
    background-position: -123px -575px !important;
  }
}

.close {
  @extend .will-change;
  width: 15px;
  height: 16px;

  &::before {
    background-position: -123px -653px !important;
  }

  &::after {
    background-position: -123px -627px !important;
  }
}

.arrow-left {
  @extend .will-change;
  width: 12px;
  height: 43px;

  &::before {
    background-position: -120px -797px !important;
  }

  &::after {
    background-position: -120px -744px !important;
  }
}

.arrow-right {
  @extend .will-change;
  width: 12px;
  height: 43px;

  &::before {
    background-position: -119px -903px !important;
  }

  &::after {
    background-position: -120px -850px !important;
  }
}

