
.main-review-holder {
  blockquote::after {
    bottom: 0;
    right: 20px;
  }

  .btn-holder {
    margin-top: @indentXl*2;
  }
}

.main-review-slider-holder {
  margin-top: @indentXl*2;
  position: relative;
  .gutter(@indentXl);
}

.main-review-block {
  padding-bottom: 100px;
  margin: 0 auto;
}

.swiper-button-next.swiper-button-next {
  text-align: center;
  width: 35px;
  top: 79px;
  right: -5px;
}

.main-review-prev.swiper-button-prev {
  text-align: center;
  width: 35px;
  top: 79px;
  right: auto;
  left: -5px;
}

.main-review-image {
  display: inline-block;
  margin-right: @indentXs;
  margin-bottom: @indentXs;

  img {
    display: block;
  }
}

.main-review-text {

  p {
    margin-bottom: 18px;
  }
}

.main-review-author {
  display: inline-block;
  font: 36px/1 "DIN Condensed";
  text-transform: uppercase;
  max-width: 535px;
  margin-top: -2px;
}

.main-review-position {
  display: inline-block;
  .b_font_h4;
  line-height: 24px;
  max-width: 535px;
  margin-bottom: 17px;
}