//==================== section course-advantages-inner
.course-advantages-block {
  .ol-counter-reset;

  li {
    // padding-left: $pad !important;
    // padding-right: $pad !important;
  }
}

.course-advantage-item {
  .li-counter-reset('.0', '');
  // @include li-counter-reset('.0', '');
  font: 18px/30px "DIN Condensed";
  text-transform: uppercase;
  letter-spacing: -.5pt;
  padding-top: 95px;
  z-index: -1;

  &:before {
    font: 24px/1 "DIN Condensed";
    top: 10px;
    left: 14px;
  }

  .arrow-block {
    display: inline-block;
    width: 55px;
    height: 46px;
    background: #fff;
    border: 5px solid @accentColor;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 100%;
      width: 0;
      height: 0;
      border-style: solid;
    }

    &:before {
      left: -5px;
      border-width: 30px 30px 0 0;
      border-color: @accentColor transparent transparent transparent;
      z-index: 1;
    }

    &:after {
      left: 0;
      border-width: 18px 18px 0 0;
      border-color: #fff transparent transparent transparent;
      z-index: 2;
    }
  }
}