//==================== section form-1
.form-1 {
  .btn-holder {
    margin-top: 25px;
    position: relative;
    z-index: 1;
  }

  .btn {
    width: 340px;
  }

  a.bold {
    margin-top: 15px;
  }

  .form-text-block {

    ul.marked {
      margin-left: 80px;
    }
  }

  .form-input-block {
    width: 260px;
    padding-top: 5px;
    margin: 0 auto;
    position: relative;
    z-index: 10;
  }
}

//==================== section form-2
.form-2 {
  height: 1035px;
  background-image: url('../img/form2-bg.jpg');

  &.form-1-inner {
    margin-top: -115px;
    padding: 525px 0 0;
  }

  &.form-2-main {
    margin-top: -154px;
  }

  h2 {
    margin-bottom: 37px;
  }

  .btn {
    width: 100%;
  }

  .form-input-block {
    width: 560px;
    margin: 0 auto;
  }
}
.form-video {
  background-position: 50%;
  margin-top: -210px;
  padding-top: 392px;
  height: 830px;
