}

.effect-item {
  .ani-hover();
  margin-bottom: 30px;
}

.effect-image {
  width: 85px;
  height: 76px;
  border: 5px solid @accentColor;
  margin-bottom: 42px;
  position: relative;

  .icon {
    .abs-center;
    font-size: 2.5em;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 100%;
    width: 0;
    height: 0;
    border-style: solid;
  }

  &::before {
    left: -5px;
    border-width: 30px 30px 0 0;
    border-color: @accentColor transparent transparent transparent;
    z-index: 1;
  }

  &::after {
    left: 0;
    border-width: 18px 18px 0 0;
    border-color: #fff transparent transparent transparent;
    z-index: 2;
  }
}

.effect-title {
  .b_font_xl;
  text-transform: uppercase;
  letter-spacing: -.05pt;
  padding-left: 40px;
  margin-bottom: 15px;
}

.effect-text {
  padding-left: 40px;
}